.select-container {
  border: 1px solid black;
  position: relative;
}
.select-box {
  border: none;
  appearance: none;
  padding: 10px;
  width: 100%;
  background-color: white;
  font-size: 20px;
}
.select-container .icon-container {
  position: absolute;
  padding: 0 3px;
  right: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}
