@media (max-width: 576px) {
    #rightNav{
        display: none;
    }
    .header{
        width: 90% !important;
        margin: auto;
        justify-content: space-around !important;
    }
    .menu-btn{
        display: block !important;
    }
    .products-list-topbar{
        flex-direction: column !important;
    }
    .loadmore{
        justify-content: center;
    }
    .footer-head{
        flex-direction: column !important;
    }
    .footer-head-card-container{
        border-radius: 5px !important;
        width: 48% !important;
    }
    .footer-head-left{
        width: 100% !important;
    }
    .social-media-icon{
        margin-right: 0.7vw !important;
    }
    .footer-cards{
        margin-top: 1vh;
        margin-bottom: 1vh;
        align-items: start !important;
        justify-content: start !important;
    }
    .footer-cards > div{
        align-self: stretch !important;
        justify-self: stretch !important;
    }
    .register{
        height: 120vh !important;
    }
    .registerImageBox{
        width: 90vw !important;
    }
    .registerContBox{
        width: 90% !important;
        flex-direction: column !important;
    }
    .registerBox{
        width: 90vw !important;
    }
    .cart-cont{
        text-align: center !important;
    }
    .cart-box-down{
        margin-top: 1vh !important;
        flex-direction: column !important;
    }
    .cart-page-btm{
        width: 100% !important;
    }
    .cart-item{
        width: 100% !important;
        flex-direction: column !important;
    }
    .product-image{
        width: 100% !important; 
    }
    .product-cart-details{
        width: 100% !important;
        padding: 5px !important;
    }
    .cart-btns{
        width: 100% !important;
        padding: 5px !important;
    }
    .make-payment-container{
        width: 100% !important;
    }
    .chk-btn{
        margin-bottom: 2vh;
    }
    .contact-top{
        flex-direction: column !important;
    }
    .contact-left{
        width: 100% !important;
    }
    .contact-right{
        width: 100% !important;
        padding: 2px !important;
    }
    .wish-text{
        font-size: 1.5rem !important;
    }
    .products-title{
        font-size: 1.7rem !important;
    }
    .admin{
        flex-direction: column !important;
    }
    .admin-sidebar{
        width: 100% !important;
        padding: 2px !important;
    }
    .admin-sidebar-logo{
        padding: 0px !important;
        margin-left: 1vw;
        font-weight: 400 !important;
        margin-bottom: 1vh !important;
    }
    #newCategory{
        width: 100% !important;
    }
    .small-btn-blue{
        width: 46%;
    }
    .page-not-found{
        margin-top: 10vh !important;
    }
    .page-nf-cont{
        width: 90% !important;
        flex-direction: column !important;
    }
    .page-nf-left{
        width: 100% !important;
    }
    .page-nf-right{
        width: 100% !important;
    }
    .page-nf-text{
        font-size: 1.7rem !important;
    }
    .page-nf-btns{
        flex-direction: column !important;
        width: 100% !important;
    }
    .page-nf-btn{
        width: 100% !important;
        margin-bottom: 1vh;
    }
    .settings-container{
        width: 100% !important;
        height: max-content !important;
        margin: 12vh auto auto auto !important;
        flex-direction: column !important;
        border: 0.5px solid black !important;
    }
    .settings-left{
        width: 100% !important;
    }
    .settings-right{
        width: 100% !important;
    }
}