.loading{
    border: 3px solid white;
    border-top: 3px solid black;
    border-right: 3px solid black;
    width: 30px;
    height: 30px;
    padding: 10px;
    border-radius: 100%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }