.card-container{
    border: 1px solid black;
    box-sizing: border-box;
    text-wrap: wrap;
    padding: 10px;
}
.card-image{
    width: 100%;
    min-height: 60%;
}
.card-image > img{
    object-fit: cover;
    display: block;
    width: 100%;
}
.card-body{
    width: 100%;
}
.card-body-top{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.prod-card-name{
    font-size: 20px;
    font-weight: 500;
}
.prod-price{
    font-size: 1.2rem;
    font-weight: 500;
}
.card-cont{
    text-align: justify;
    width: 100%;
    box-sizing: border-box;
    word-wrap: break-word;
}

.card-footer{
    padding: 5px 2px;
}
.card-footer-btn{
    border: 1px solid black;
    background-color: white;
    color: black;
    padding: 6px;
    border-radius: 5px;
    align-self: stretch;
}
.b-large{
    border: 1px solid black;
    background-color: black;
    color: white;
    width: 100%;
    margin: 0 0.5vw;
    align-self: stretch;
    transition: 0.2s;
}
.b-large:hover{
    background-color: white;
    color: black;
}