#newCategory{
    width: 60%;
    padding: 3px 2px;
}

.searchForm{
    width: 100%;
    margin: 0 1vw;
}

#search{
    align-self: stretch;
    padding: 8px 5px;
    width: 70%;
    border: 0.3px solid black;
    border-radius: 8px 0 0 8px;
}
#search:focus{
    outline: none;
}

.search-btn{
    cursor: pointer;
    border-radius: 0 8px 8px 0;
    background-color: black;
    border: none;
    color: white;
    align-self: stretch;
    padding: 5px;
}