* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  /* font-family: "Montserrat", sans-serif; */
}

.page-not-found{
  width: 100vw;
  height: 100vh;
}
.page-nf-cont{
  width: 70%;
  height: auto;
}
.page-nf-left{
  width: 30%;
  height: 100%;
}
.page-nf-left > img{
  display: block;
  object-fit: cover;
  width: 100%;
}
.page-nf-right{
  width: 70%;
  padding: 0 5px;
}
.page-nf-text{
  font-size: 2.3rem;
  font-weight: 300;
}
.page-nf-btn{
  text-decoration: none;
  color: inherit;
  border: 2px solid black;
  width: 180px;
  height: 40px;
  transition: background-color 0.3s;
}
.page-nf-btn:hover{
  background-color: black;
  color: white;
}
.page-nf-btns{
  width: max-content;
}

.header {
  width: max-content;
  padding: 4px;
  backdrop-filter: blur(14px) saturate(51%);
  -webkit-backdrop-filter: blur(14px) saturate(51%);
  background-color: rgba(255, 255, 255, 0.74);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  position: fixed;
  top: 2vh;
  left: 50%;
  z-index: 5;
  transform: translateX(-50%);
}
.header > div {
  align-self: stretch;
  justify-self: stretch;
}
.menu-header{
  display: none;
  position: fixed;
  top: 100%;
  left: 0;
  z-index: 5;
  margin-top: 2vh;
  width: 100%;
  padding: 4px;
  backdrop-filter: blur(14px) saturate(51%);
  -webkit-backdrop-filter: blur(14px) saturate(51%);
  background-color: rgba(255, 255, 255, 0.74);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  list-style: none;
}
.closeCont{
  padding: 5px;
}
.close-btn{
  z-index: 6;
}
.close-btn:hover{
  color: red;
}
.logoName {
  font-size: 2rem;
  text-decoration: none;
  color: inherit;
  font-family: "Montserrat", sans-serif;
}
.header-navlist{
  padding: 5px;
  width: 100%;
  margin-top: 1vh;
  transition: 0.2s;
}
.header-navlist:hover{
  background-color: #272829;
  color: white;
}
.navlink{
  text-decoration: none;
  color: inherit;
}

.container {
  width: 100vw;
  overflow: scroll;
}
.layout {
  width: 100%;
  flex: 1;
  align-self: stretch;
  justify-self: stretch;
}
.home {
  width: 100%;
}
.logo{
  margin-right: 2vw;
}
.rightNav {
  width: max-content;
  margin-right: 2vw;
}
.navbar {
  width: max-content;
  list-style-type: none;
}
.rightNav-navlink {
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;
  opacity: 0.6;
  padding: 5px;
  margin-inline: 1vw;
}
.btns {
  width: max-content;
  margin-right: 2vw;
}
.btn {
  border: 1px solid black;
  text-decoration: none;
  color: inherit;
  padding: 7px 8px;
  margin-inline: 0.2vw;
  transition: all 0.2s;
}
.menu-btn{
  display: none;
}
.menu-btn:hover{
  background-color: black;
  color: white;
}
.loginBtn:hover {
  color: white;
  background-color: black;
  border: 1px solid black;
}
.regBtn {
  color: white;
  background-color: rgb(0, 0, 0);
}
.regBtn:hover {
  background-color: rgb(54, 54, 54);
}
.cartBtn {
  font-size: 18px;
}
.cartBtn:hover {
  color: white;
  background-color: black;
  border: 1px solid black;
}

.left {
  text-align: justify;
  width: 60%;
}
.right {
  width: 40%;
}
.right > img {
  display: block;
  object-fit: contain;
  width: 100%;
}

.register {
  width: 100%;
  height: 100vh;
}
.registerContBox {
  width: 80%;
  aspect-ratio: 16/9;
  padding: 5px;
  margin: auto;
  border-radius: 40px;
}
.registerImageBox {
  width: 70vw;
}
.registerImageBox > img {
  width: 100%;
  display: block;
  object-fit: contain;
}
.registerBox {
  padding: 10px;
  width: 60vw;
}
.registerHead {
  width: 100%;
  padding: 5px;
}
.registerSubHead{
  opacity: 0.6;
}
.registerBody {
  padding: 5px;
}
.upbox{
  width: 100%;
}
.passwordCont {
  width: 100%;
  position: relative;
}
.showPasswordBtn {
  background-color: transparent;
  border: none;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
}
.registerForm {
  width: 100%;
}
.alink{
  font-size: 16px;
}
.form-control {
  width: 100%;
  padding: 12px 8px;
}
.form-control-r{
  width: 100%;
  padding: 10px;
  margin: 0.2vw 0;
}
.formSubmitBtn{
  width: 100%;
}
.form-action {
  padding: 12px 8px;
  width: 100%;
  margin-right: 0.5vw;
  cursor: pointer;
  justify-items: start;
}
.form-submit {
  background-color: #212529;
  color: #f8f9fa;
  border: 1px solid #212529;
  transition: all 0.2s;
}
.form-submit:hover{
  background-color: #f8f9fa;
  color: #212529;
  border: 1px solid #212529;
}
.form-reset {
  background-color: #dc3545;
  color: #f8f9fa;
  border: 1px solid #dc3545;
}
.registerFooter {
  width: 100%;
}
.spinner {
  height: 100%;
}
.hide-header{
  display: block;
}

.carousel-cont {
  width: 100%;
  margin: auto;
}
.carousel-image {
  display: block;
  width: 100%;
  object-fit: cover;
}

.dropdown {
  backdrop-filter: blur(14px) saturate(51%);
  -webkit-backdrop-filter: blur(14px) saturate(51%);
  background-color: rgba(255, 255, 255, 0.74);
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  width: max-content;
  z-index: 6;
  display: none;
  padding: 5px 15px;
  margin-top: 1vh;
  position: absolute;
  top: 100%;
}
.dropdown-link {
  margin: 0.2vh 0;
}
.show-dropdown {
  display: flex;
}
.dropdown-link:hover {
  opacity: 1;
}

.list-group {
  padding: 10px;
}
.list-group-item {
  margin: 1vh 0;
}
.list-link {
  text-decoration: none;
  color: inherit;
}
.prod-cart-title{
  font-size: 20px;
}
.prod-cart-price{
  font-size: 18px;
  font-weight: 500;
}
.prod-cart-total{
  font-size: 18px;
}

.admin-dashboard {
  padding: 10px 2px;
  margin-top: 10vh;
}
.admin-header {
  position: relative;
}
.admin-btn {
  border: 1px solid black;
  padding: 7px 8px;
  transition: all 0.2s;
}
.admin-btn:hover {
  background-color: black;
  color: white;
}
.admin-card {
  display: none;
  border: 0.5px solid black;
  background-color: white;
  width: max-content;
  padding: 5px;
  position: absolute;
  top: 40px;
  right: 0;
  z-index: 5;
}
.admin-card-head {
  padding: 5px;
}
.admin-card-body {
  padding: 5px;
}
.detail {
  font-weight: 400;
  margin-left: 5px;
}
.admin-sidebar {
  width: 50%;
  padding: 5px 10px;
}
.admin-sidebar-logo {
  padding: 8px 7px;
  font-weight: 500;
  font-size: 20px;
}
.admin-head-text{
  font-size: 1.3rem;
  font-size: 420;
}
.sidenav-item {
  list-style-type: none;
}
.sidenav-link {
  color: inherit;
  text-decoration: none;
}
.admin-contents {
  width: 100%;
  padding: 5px;
  align-self: stretch;
}
.showAdminCard {
  display: block;
}

.footer {
  background-color: #272829;
  color: whitesmoke;
  padding: 5px;
  width: 100%;
}
.footer-container {
  padding: 5px;
}
.footer-logo {
  font-size: 20px;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
}
.footer-head-left {
  width: 40%;
}
.footer-cont {
  font-size: medium;
  font-weight: 200;
  padding: 2px 0;
}
.social-media {
  padding: 5px 0;
}
.social-media-icon {
  padding: 5px;
  text-decoration: none;
  margin-right: 0.2vw;
  border: 1px solid white;
  background-color: white;
  color: black;
  border-radius: 100%;
  transition: all 0.2s;
}
.social-media-icon:hover {
  background-color: #212529;
  color: white;
}
.footer-cards {
  width: 100%;
  /* align-self: stretch; */
  flex-wrap: wrap;
}
.footer-head-card-container {
  width: 150px;
  height: 150px;
  padding: 8px;
  border-radius: 20px;
  opacity: 0.7;
  margin-inline: 0.5vw;
  cursor: pointer;
  transition: all 0.2s;
}
.footer-head-card-container:hover {
  background-color: white;
  color: #212529;
  transform: translate(-5px, 5px);
}
.footer-head-card-text {
  text-align: justify;
  margin-top: 2vh;
}
.footer-vertical-header {
  width: 40%;
  align-self: stretch;
}
.v-list {
  width: 100%;
  opacity: 0.5;
}
.v-list > li {
  margin: 0.2vh 2vw;
  list-style-type: none;
  transition: all 0.2s;
}
.v-list > li:hover {
  opacity: 1;
}
.footer-body {
  padding: 5px;
}
.footer-body-top {
  padding: 5px;
  flex-wrap: wrap;
}
.footer-navlink {
  padding: 2px 0;
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;
  opacity: 0.6;
  margin-inline: 1vw;
}
.footer-body-bottom {
  margin-top: 1vh;
}
#subsEmail {
  width: 200px;
  padding: 8px 5px;
  border-radius: 5px;
  border: none;
}
#subsEmail:focus {
  outline: none;
}
.footer-subs-btn {
  padding: 8px 5px;
  border: 1px solid #dc3545;
  border-radius: 5px;
  margin-left: 0.5vw;
  background-color: #dc3545;
  color: white;
  cursor: pointer;
  transition: all 0.2s;
}
.footer-subs-btn:hover {
  background-color: #272829;
  color: #dc3545;
}
.active {
  opacity: 1;
  border-bottom: 2px solid #212529;
}

/* Category Table */
.category-table {
  width: 100%;
  padding: 5px;
}
.col-2 {
  width: 60%;
}
.col-1,
.col-2 {
  padding: 3px;
  text-align: left;
}

.sidenav-link:active{
  color: blue;
}


.file-upload {
  position: relative;
  overflow: hidden;
  display: inline-block;
}
.file-input {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.custom-button {
  background-color: white;
  border: 1px solid black;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-top: 10px;
  cursor: pointer;
}
.preview-box{
  width: 50%;
  height: auto;
}
.preview-box > img{
  display: block;
  object-fit: cover;
  width: 100%;
}

.products-container{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(300px, 1fr));
  gap: 10px;
}
.products-title{
  font-size: 2rem;
}
.card-link{
  text-decoration: none;
  color: inherit;
}

.settings{
  height: 100vh;
}
.settings-container{
  width: 80%;
  height: 80%;
  margin: auto;
}
.settings-left{
  width: 30%;
  border-right: 1px solid black;
  padding: 5px;
}
.settings-left-top{
  height: 100%;
  margin: 1vh 0;
}
.settings-link{
  padding: 8px 2px;
  margin-bottom: 0.5vh;
  text-decoration: none;
  color: inherit;
  border: 1px solid black;
  transition: all 0.2s;
}
.settings-link:hover{
  background-color: #272829;
  color: #f8f9fa;
}
.logout-navlink{
  padding: 8px 2px;
  margin: 0.5vh 0;
  text-decoration: none;
  color: #dc3545;
  border: 1px solid #dc3545;
  transition: background-color 0.4s;
}
.logout-navlink:hover{
  background-color: #dc3545;
  color: white;
}
.settings-right{
  width: 70%;
  padding: 5px;
}
.settings-right-box{
  width: max-content;
}


.search-page{
  margin-top: 10vh;
}

.cart-page{
  width: 100vw;
  height: 90vh;
  margin-top: 10vh;
  position: relative;
  overflow: scroll;
}
.greet-user{
  font-size: 22px;
  font-weight: 550;
  font-family: "Montserrat", sans-serif;
}
.cart-cont{
  font-size: 22px;
  font-family: "Montserrat", sans-serif;
}
.cart-btn{
  position: relative;
}
.cart-page-btm{
  width: 60%;
  overflow-y: scroll !important;
}
.make-payment-container{
  width: 40%;
}
.cart-item{
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2vh;
}
.product-image{
  width: 30%;
}
.product-image > img{
  width: 100%;
  object-fit: cover;
}
.product-cart-details{
  align-self: stretch;
  justify-self: stretch;
  padding: 10px;
  width: 50%;
}
.cart-btns{
  align-self: stretch;
  justify-self: stretch;
  padding: 10px;
  width: 30%;
}
.remove-item-btn{
  padding: 10px;
  background-color: #dc3545;
  color: white;
  border: 1px solid #dc3545;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s;
}
.remove-item-btn:hover{
  background-color: white;
  color: #dc3545;
}
.move-item-btn{
  padding: 10px;
  background-color: white;
  color: #dc3545;
  border: 1px solid #dc3545;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s;
}
.move-item-btn:hover{
  background-color: #dc3545;
  color: #f8f9fa;
}
.qut-btn{
  background-color: white;
  color: black;
  border: none;
  margin-inline: 0.5vw;
}
.chk-btn{
  padding: 5px 10px;
  background-color: #212529;
  color: white;
  border: 1px solid #212529;
  cursor: pointer;
  transition: all 0.2s;
}
.chk-btn:hover{
  background-color: white;
  color: #212529;
}

.cart-sum{
  font-size: 25px;
  font-weight: 450;
  font-family: "Montserrat", sans-serif;
}
.tcp{
  font-size: 18px;
  font-weight: 400;
  font-family: "Montserrat", sans-serif;
}
.total-price{
  font-size: 20px;
  font-weight: 450;
  font-family: "Montserrat", sans-serif;
}


.wishlist-page{
  margin-top: 10vh;
}
.wish-text{
  padding: 10px;
  font-size: 2rem;
}

.about-title{
  font-size: 30px;
}
.contact{
  margin-top: 10vh;
}
.contact-top{
  width: 95%;
  margin: 0 auto;
  border: 0.5px solid black;
  border-radius: 15px;
}
.contact-btm{
  width: 95%;
  margin: 1vh auto 3vh auto;
  border-radius: 15px;
}
.contact-left{
  width: 40%;
}
.git-text{
  padding: 5px;
}
.contact-right{
  width: 60%;
  padding: 5px;
}
.contact-left > img{
  display: block;
  object-fit: cover;
  width: 100%;
  border-radius: 15px;
}
.contactForm{
  width: 100%;
  margin: auto;
  padding: 10px;
}
.contactForm > input,
.contactForm > textarea{
  width: 100%;
  display: inline-block;
  padding: 10px;
  margin-top: 1vh;
}
.contactForm > input:focus,
.contactForm > textarea:focus{
  outline: 1px solid #212529;
}
.query-submit-btn{
  width: 140px;
  height: 35px;
  padding: 10px;
  margin-top: 1vh;
  border: 1px solid #212529;
  background-color: #212529;
  color: #f8f9fa;
  cursor: pointer;
  transition: background-color 0.2s;
}
.query-submit-btn:hover{
  background-color: #f8f9fa;
  color: #212529;
}

.contact-btm{
  padding: 10px;
}
.faqs-title{
  font-size: 2rem;
  margin-bottom: 0.4vh;
}
.fu-title{
  font-size: 1.4rem;
}
.faqs{
  width: 100%;
}
.faq{
  border: 1px solid black;
  padding: 12px 5px;
  cursor: pointer;
  width: 100%;
}
.faq-head{
  font-size: 18px;
  font-weight: 300;
}
.faq-btm{
  font-size: 18px;
  font-weight: 300;
  display: none;
}
.show-faq{
  display: block;
}

.about{
  height: 100vh;
}


.categories-page{
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10vh;
  padding: 5px;
}
.categories-grid{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 10px;
}
.category-box{
  width: 200px;
  height: 200px;
  border-radius: 15px;
  transition: 0.2s;
}
.category-box:hover{
  color: white;
  background-color: #272829;
}

.product-title{
  font-size: 20px;
  font-weight: 400;
  padding: 5px 0;
}

.orders-page{
  margin-top: 10vh;
}
.orders-title{
  font-size: 2rem;
}