.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  width: 50vw;
  height: auto;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}
.modal-header{
  width: 100%;
}
.modal-body{
  width: 100%;
}

.modal-close-btn {
  padding: 5px;
  cursor: pointer;
  border: none;
  background-color: white;
}
