.products-list {
  width: 100vw;
  padding: 10px 5px;
  margin: 1vh 0;
}
.products-list-topbar-right {
  position: relative;
  width: auto;
}
.products-list-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(400px, 1fr));
  gap: 10px;
}

/*  Filters  */
.filters {
  border: 0.5px solid black;
  display: none;
  padding: 5px;
  margin-top: 0.2vh;
  backdrop-filter: blur(14px) saturate(51%);
  -webkit-backdrop-filter: blur(14px) saturate(51%);
  background-color: rgba(255, 255, 255, 0.74);
  position: absolute;
  z-index: 3;
  top: 45px;
  left: 0;
}
.filter {
  margin-bottom: 0.5vw;
}
.filter-btn {
  width: 150px;
  height: 40px;
  cursor: pointer;
  padding: 5px;
  border: none;
  /* margin-right: 1vw; */
  width: max-content;
  background-color: black;
  color: white;
}
.filter-btn-red {
  width: 150px;
  height: 40px;
  cursor: pointer;
  padding: 5px;
  border: none;
  margin-right: 1vw;
  width: max-content;
  background-color: #dc3545;
  color: white;
}
.filter > label {
  display: inline-block;
  margin-left: 3px;
  color: rgb(0, 0, 0);
  cursor: pointer;
}
.hide-filters {
  display: block;
}

.pos-rel{
  position: relative;
}
.count{
  background-color: #dc3545;
  color: white;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
}

.loadmore-btn{
  width: fit-content;
  padding: 10px 8px;
  margin-top: 1vh;
  background-color: black;
  color: white;
  cursor: pointer;
}

.product-details{
  margin-top: 10vh;
}



.price{
  font-size: 1.4rem;
  font-weight: 400;
}

.product-details-btm{
  width: 100%;
  height: 50%;
}
.product-details-btm-left{
  width: 40%;
}
.product-details-btm-left > img{
  display: block;
  object-fit: cover;
  width: 100%;
}
.product-details-btm-right{
  width: 60%;
  padding: 10px;
}
.product-detail-right-btns{
  width: 30%;
}
.footer-btn{
  border: 1px solid black;
  background-color: white;
  color: black;
  padding: 6px;
  border-radius: 5px;
  align-self: stretch;
}
.p-large{
  border: 1px solid black;
  background-color: black;
  color: white;
  width: 100%;
  margin-right: 0.5vw;
  align-self: stretch;
  transition: 0.2s;
}
.p-large:hover{
  background-color: white;
  color: black;
}

.similar-products-cont{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(400px, 1fr));
  gap: 10px;
}
.p-l{
  padding: 5px 10px;
}